import React, { Component } from "react";
import InputColor from "react-input-color";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

class Onboarding extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectionHistory: { 1: false, 2: false, 3: false, 4: false }
    };
  }
  handleColor = color => {
    this.setState({ color });
  };

  handleSelection = (index, bool) => {
    this.setState({
      selectionHistory: { ...this.state.selectionHistory, [index]: bool }
    });
  };
  render() {
    return (
      <div className="signin-wrapper">
        <div className="signin-box onboarding">
          <h2 className="slim-logo">
            <a href="index.html">
              <img
                src={require("../../img/frontegg-logo.png")}
                alt="placeholder"
              />
            </a>
          </h2>
          <h3 className="signin-title-primary">Setup your Account!</h3>
          <h5 className="signin-title-secondary lh-4 mg-b-30">
            Few more details to continue into your Frontegg
          </h5>

          <div className="form-layout form-layout-7 mg-b-20">
            <div className="row no-gutters">
              <div className="col-5 col-sm-4">Application Name</div>
              <div className="col-7 col-sm-8">
                <input
                  className="form-control"
                  type="text"
                  name="fullname"
                  placeholder="Enter your application name"
                />
              </div>
            </div>
            <div className="row no-gutters">
              <div className="col-5 col-sm-4">Frontend Stack</div>
              <div className="col-7 col-sm-8">
                <div className="card-columns card-columns-2 mg-t-20">
                  <div
                    className={`card card-body pd-15 tx-center ${
                      this.state.selectionHistory[1] === true
                        ? "selectCard"
                        : null
                    }`}>
                    <div className="card-icon-wrapper mg-b-0 ht-80">
                      <i className="icon fab fa-react" />
                    </div>
                    <h5 className="tx-gray-800 mg-b-25">ReactJS</h5>
                    <Link
                      to="#"
                      className="btn btn-primary btn-block"
                      onClick={() =>
                        this.state.selectionHistory[1] === true
                          ? this.handleSelection(1, false)
                          : this.handleSelection(1, true)
                      }>
                      {this.state.selectionHistory[1] === true
                        ? "Unselect"
                        : "Select"}
                    </Link>
                  </div>

                  <div
                    className={`card card-body pd-15 tx-center ${
                      this.state.selectionHistory[2] === true
                        ? "selectCard"
                        : null
                    }`}>
                    <div className="card-icon-wrapper mg-b-0 ht-80">
                      <i className="icon fab fa-angular" />
                    </div>
                    <h5 className="tx-gray-800 mg-b-25">Angular</h5>
                    <Link
                      to="#"
                      className="btn btn-primary btn-block"
                      onClick={() =>
                        this.state.selectionHistory[2] === true
                          ? this.handleSelection(2, false)
                          : this.handleSelection(2, true)
                      }>
                      {this.state.selectionHistory[2] === true
                        ? "Unselect"
                        : "Select"}
                    </Link>
                  </div>
                </div>
              </div>
            </div>
            <div className="row no-gutters">
              <div className="col-5 col-sm-4">Backend Stack</div>
              <div className="col-7 col-sm-8">
                <div className="card-columns card-columns-2 mg-t-20">
                  <div
                    className={`card card-body pd-15 tx-center ${
                      this.state.selectionHistory[3] === true
                        ? "selectCard"
                        : null
                    }`}>
                    <div className="card-icon-wrapper mg-b-0 ht-80">
                      <i className="icon fab fa-node" />
                    </div>
                    <h5 className="tx-gray-800 mg-b-25">NodeJS</h5>
                    <Link
                      to="#"
                      className="btn btn-primary btn-block"
                      onClick={() =>
                        this.state.selectionHistory[3] === true
                          ? this.handleSelection(3, false)
                          : this.handleSelection(3, true)
                      }>
                      {this.state.selectionHistory[3] === true
                        ? "Unselect"
                        : "Select"}
                    </Link>
                  </div>
                  <div
                    className={`card card-body pd-15 tx-center ${
                      this.state.selectionHistory[4] === true
                        ? "selectCard"
                        : null
                    }`}>
                    <div className="card-icon-wrapper mg-b-0 ht-80">
                      <i className="icon fab fa-python" />
                    </div>
                    <h5 className="tx-gray-800 mg-b-25">Python</h5>
                    <Link
                      to="#"
                      className="btn btn-primary btn-block"
                      onClick={() =>
                        this.state.selectionHistory[4] === true
                          ? this.handleSelection(4, false)
                          : this.handleSelection(4, true)
                      }>
                      {this.state.selectionHistory[4] === true
                        ? "Unselect"
                        : "Select"}
                    </Link>
                  </div>
                </div>
              </div>
            </div>
            <div className="row no-gutters">
              <div className="col-5 col-sm-4">Upload Logo</div>
              <div className="col-7 col-sm-8">
                <div className="custom-file">
                  <input
                    type="file"
                    className="custom-file-input"
                    id="customFile"
                  />
                  <label className="custom-file-label" htmlFor="customFile">
                    Choose or Drop
                  </label>
                </div>
              </div>
            </div>
            <div className="row no-gutters">
              <div className="col-5 col-sm-4">Primary Color</div>
              <div className="col-7 col-sm-8">
                <InputColor
                  initialHexColor="#5e72e4"
                  onChange={this.handleColor}
                  placement="bottom"
                />
              </div>
            </div>
            <div className="row no-gutters">
              <div className="col-5 col-sm-4">Secondary Color</div>
              <div className="col-7 col-sm-8">
                <InputColor
                  initialHexColor="#5e72e4"
                  onChange={this.handleColor}
                  placement="bottom"
                />
              </div>
            </div>
          </div>
          <Link to="/dashboard/configure-component">
            <button className="btn btn-primary btn-block btn-signin">
              Continue
            </button>
          </Link>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  // user: state.user
});

const mapDispatchToProps = dispatch => ({
  // login: payload => dispatch(login(payload))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Onboarding);
