import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

// action creators
import { login } from "../../redux/actions";

class SignIn extends Component {
  login = () => {
    const userObject = {
      email: "eve.holt@reqres.in",
      password: "cityslicka"
    };
    this.props.login(userObject);
    this.props.history.push("/onboarding");
  };
  render() {
    return (
      <div className="signin-wrapper">
        <div className="signin-box">
          <h2 className="slim-logo">
            <Link to="/">
              <img src={require("../../img/frontegg-logo.png")} alt="logo" />
            </Link>
          </h2>
          <h2 className="signin-title-primary">Welcome back!</h2>
          <h3 className="signin-title-secondary">Sign-in to continue.</h3>

          <div className="form-group">
            <input
              type="text"
              className="form-control"
              placeholder="Enter your email"
            />
          </div>
          <div className="form-group mg-b-50">
            <input
              type="password"
              className="form-control"
              placeholder="Enter your password"
            />
          </div>
          {/* <Link to="/onboarding"> */}
          <button
            className="btn btn-primary btn-block btn-signin"
            onClick={this.login}>
            Sign In
          </button>
          {/* </Link> */}
          <p className="mg-b-0">
            Don't have an account? <Link to="/signup">Sign Up</Link>
          </p>
          <p className="mg-b-0">
            Forgot your password? <Link to="/forgot">Click Here</Link>
          </p>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  // user: state.user
});

const mapDispatchToProps = dispatch => ({
  login: payload => dispatch(login(payload))
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SignIn);
